<template>
  <div id="error-content">
    <div class="icon">
    <lottie-player
        autoplay
        loop
        mode="normal"
        style="width: 150px"
    ></lottie-player>
    </div>
    <div class="title">{{ $store.state.lang === 'ru' ? 'Ваша платформа не поддерживается' : 'Sizning platformangiz qo\'llab-quvvatlanmaydi' }}</div>
    <div class="desc" v-if="$store.state.lang === 'ru'">Gold Ticket не может быть открыт на вашей платформе <span id="device"></span>, пожалуйста, откройте эту услугу с мобильной платформы Telegram</div>
    <div class="desc" v-else>Gold Ticket <span id="device"></span> Platforamsida ochib bo'lamydi, Iltimos Telegramning mobil dasturida oching</div>
  </div>
</template>

<script>
import ErrorAnimation from "@/files/374692314018741028.json";

export default {
  name: "Errors",
  mounted() {
    let device;
    device = Telegram.WebApp.platform === 'tdesktop' ? 'Telegram Desktop' : Telegram.WebApp.platform === 'webz' ? 'Telegram Web' : '' ;
    document.getElementById('device').innerText = device

    const player = document.querySelector("lottie-player");
    player.addEventListener("rendered", (e) => {
      //Load via URL
      player.load(ErrorAnimation);
    });
  }
}
</script>

<style scoped>
body {
  overflow: hidden;
}
#error-content {
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  padding: 0 35px;
  top: -25px;
  left: 0;
  right: 0;
  bottom: 0;
}

#device {
  color: #ffd527;
}

.icon lottie-player{
  width: 150px;
  height: 150px;
}

.title {
  margin-top: 25px;
  font-size: 20px;
  font-weight: bold;
}

.desc {
  margin-top: 15px;
  color: var(--tg-theme-hint-color);;
}
</style>
